.submit__button-container {
  display: flex;
  justify-content: flex-end;
}

@media screen and (max-width: 500px) and (min-width: 320px) {
  .submit__button-container {
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
  }
}
